import { FETCH_ALL_USERS, UPDATE_ROLES_FOR_USER, DELETE_USERS } from '../constants/actionTypes';
import initialState from './initialState';

export default function adminReducer(state = initialState.users, action) {
  switch (action.type) {
    case FETCH_ALL_USERS:
      return action.payload;
    case UPDATE_ROLES_FOR_USER: {
      return state.slice().map(user => {
        if (user.username === action.payload.username) {
          return {...user, ...action.payload};
        }
        return user;
      });
    }
    case DELETE_USERS:
    return state.filter(user => !action.payload.includes(user.username));
    default:
      return state;
  }
}
