import { getApiUri } from '../utils';
import apiFetch from './apiFetch';
import jsonHeadersWithToken from './jsonHeadersWithToken';
import resolveToken from './resolveToken';
import URI from 'URIjs';
import * as types from '../constants/actionTypes';

export function getUsers() {
    return function(dispatch) {
      let resolvedToken = resolveToken('');
      var uri = URI(getApiUri()).segment('users');
      return apiFetch(dispatch, uri, {
        method: 'GET',
        headers: jsonHeadersWithToken(resolvedToken)
    }).then(response => {
        return response.json();
      }).then(json => {
        dispatch(setFetchedUsers(json));
      });
    }
  }

  export function updateUsers(username, roles) {
    return function(dispatch) {
      let resolvedToken = resolveToken('');
      var uri = URI(getApiUri()).segment('users').segment(username).segment('roles');
      return apiFetch(dispatch, uri, {
        method: 'POST',
        headers: jsonHeadersWithToken(resolvedToken),
        body: JSON.stringify(roles)
    }).then(response => {
        return response.json();
      }).then(json => {
        dispatch(updateRolesForUsername(username, json));
      });
    }
  }

  export function deleteUsers(usernames) {
    return function(dispatch) {
      let resolvedToken = resolveToken('');
      var uri = URI(getApiUri()).segment('users');
      return apiFetch(dispatch, uri, {
        method: 'DELETE',
        headers: jsonHeadersWithToken(resolvedToken),
        body: JSON.stringify(usernames)
    }).then(response => {
        return response.json();
      }).then(json => {
        if (json)
        dispatch(updateDeletedUsers(usernames));
      });
    }
  }

export function setFetchedUsers(users) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.FETCH_ALL_USERS,
      payload: users
    });
  };
}

export function updateRolesForUsername(username, roles) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.UPDATE_ROLES_FOR_USER,
      payload: {username, roles}
    });
  };
}

export function updateDeletedUsers(usernames) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.DELETE_USERS,
      payload: usernames
    });
  };
}