import React from 'react';
import {Redirect} from 'react-router-dom';
import {connect} from 'react-redux';
import PropTypes from "prop-types";
import Button from '@material-ui/core/Button';
import jwt from 'jwt-decode';

class LandingPage extends React.Component {

  constructor(props) {
    super(props);
    this.renderRoles = this.renderRoles.bind(this);
    this.onButtonClick = this.onButtonClick.bind(this);
  }

  renderRoles(roles) {
    if (roles.length > 1) {
      const rolesButtons = roles.map(role => (
        <Button key={role} id={role} name={role} variant="contained" color="secondary" onClick={this.onButtonClick}>
        {role}
        </Button>
      ));
      return rolesButtons
    }
    return <p></p>;

  }


  onButtonClick(event) {
    this.props.history.push(event.target.name);
  }


  render() {
    if (!this.props.isUserLoggedIn) {
      return <Redirect to="/"/>;
    }
    const roles = jwt(localStorage.getItem('token')).roles;
    const buttons =  this.renderRoles(roles);
    return (
      <div>
      <h1>Hello, you are on the landing page</h1>
      {(roles.length > 1) && <h2> Click the role you want to log in as: </h2>}
      {buttons}
      </div>
    );
  }
}

LandingPage.propTypes = {
  isUserLoggedIn: PropTypes.bool.isRequired,
  history: PropTypes.object
};


function mapStateToProps(state) {
  return {
    isUserLoggedIn: state.isUserLoggedIn
  };
}


export default connect(
  mapStateToProps,
  null
)(LandingPage);


