import React from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PropTypes from "prop-types";
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import Button from '@material-ui/core/Button';
import TableBody from '@material-ui/core/TableBody';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Checkbox from '@material-ui/core/Checkbox';
import ListItemText from '@material-ui/core/ListItemText';
import Select from '@material-ui/core/Select';
import Input from '@material-ui/core/Input';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { CustomTableCell } from '../common/CustomTableCell';
import EnhancedTableToolbar from '../common/EnhancedTableToolbar';
import Snackbar from '@material-ui/core/Snackbar';
import * as adminActions from '../../actions/adminActions';

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  mainDiv: {
    flexGrow: 1
  },
  table: {
    minWidth: 700,
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
    maxWidth: 300,
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    color: 'green'
  }
});


const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const roles = ["user", "admin", "omu-negru"];

class AdminPage extends React.Component {

  constructor(props) {
    super(props);
    this.selectRole = this.selectRole.bind(this);
    this.makeSelect = this.makeSelect.bind(this);
    this.createMuiRowFromUser = this.createMuiRowFromUser.bind(this);
    this.createHeader = this.createHeader.bind(this);
    this.makeMuiTable = this.makeMuiTable.bind(this);
    this.onRowClick = this.onRowClick.bind(this);
    this.isSelected = this.isSelected.bind(this);
    this.areAllSelected = this.areAllSelected.bind(this);
    this.onSelectAllClick = this.onSelectAllClick.bind(this);
    this.openAlertWindow = this.openAlertWindow.bind(this);
    this.closeAlertWindow = this.closeAlertWindow.bind(this);
    this.renderAlertWindow = this.renderAlertWindow.bind(this);
    this.handleNo = this.handleNo.bind(this);
    this.handleYes = this.handleYes.bind(this);
    this.handleShowDeleteSnackbar = this.handleShowDeleteSnackbar.bind(this);
    this.state = { alertOpen: false,  selected: [], showDeleteSnackbar: false }
    props.adminActions.getUsers();
  }

  openAlertWindow() {
    this.setState({ alertOpen: true });
  }

  closeAlertWindow() {
    this.setState({ alertOpen: false });
  }

  handleNo() {
    this.setState({ alertOpen: false });
  }

  handleShowDeleteSnackbar() {
    this.setState({ showDeleteSnackbar: false });
  }

  handleYes() {
    this.props.adminActions.deleteUsers(this.state.selected);
    this.setState({ selected: [] });
    this.setState({ alertOpen: false,  showDeleteSnackbar: true });
  }

  onSelectAllClick() {
    if (this.state.selected.length === this.props.users.length) {
      this.setState({ selected: [] });
    } else {
      const newState = this.props.users.map(t => t.username);
      this.setState({ selected: newState });
    }
  }

  areAllSelected() {
    return this.state.selected.length === this.props.users.length;
  }

  isSelected(username) {
    return this.state.selected.includes(username);
  }


  onRowClick(username) {
    const newState = this.state.selected.slice();
    const index = newState.indexOf(username);
    if (index > -1) {
      newState.splice(index, 1);
    } else {
      newState.push(username);
    }
    this.setState({ selected: newState });
  }

  createMuiRowFromUser(user, classes) {
    const isSelected = this.isSelected(user.username);
    return (
      <TableRow
        key={user.username}
        hover
        role="checkbox"
        onClick={() => this.onRowClick(user.username)}
      >
        <CustomTableCell padding="checkbox"> <Checkbox
          checked={isSelected}
        /></CustomTableCell>
        <CustomTableCell component="th" scope="row">{user.fullName}</CustomTableCell>
        <CustomTableCell>{user.username}</CustomTableCell>
        <CustomTableCell>{this.makeSelect(user.username, user.roles, classes)}</CustomTableCell>
      </TableRow>
    );
  }

  renderAlertWindow() {
    return (<div>
      <Dialog
        open={this.state.alertOpen}
        onClose={this.handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Delete selected users"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure you want to delete the selected users?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleNo} color="primary">
            No
          </Button>
          <Button onClick={this.handleYes} color="primary" autoFocus>
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    </div>);
  }

  renderSnackbar() {
    return( <Snackbar
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={this.state.showDeleteSnackbar}
      autoHideDuration={6000}
      onClose={this.handleShowDeleteSnackbar}
      ContentProps={{
        'aria-describedby': 'message-id',
      }}
      message={<span id="message-id">User deleted</span>}
    />);
  }


  createHeader() {
    const areAllSelected = this.areAllSelected()
    const indeterminate = this.state.selected > 0 && this.state.selected < this.props.users;
    return (
      <TableHead>
        <TableRow>
          <CustomTableCell padding="checkbox"> <Checkbox
            indeterminate={indeterminate}
            checked={areAllSelected}
            onChange={this.onSelectAllClick}
          /></CustomTableCell>
          <CustomTableCell>Full Name </CustomTableCell>
          <CustomTableCell>Username</CustomTableCell>
          <CustomTableCell>Assigned Roles</CustomTableCell>
        </TableRow>
      </TableHead>
    );
  }

  makeMuiTable(users, classes) {
    const rows = users.map(user => this.createMuiRowFromUser(user, classes))
    return (
      <TableBody>
        {rows}
      </TableBody>
    );
  }

  selectRole(event) {
    this.props.adminActions.updateUsers(event.target.name, event.target.value);
  }

  makeSelect(username, userRoles, classes) {
    return (
      <FormControl className={classes.formControl}>
        <Select
          multiple
          name={username}
          value={userRoles}
          onChange={this.selectRole}
          input={<Input id="select-multiple-checkbox" />}
          renderValue={selected => selected.join(', ')}
          MenuProps={MenuProps}
        >
          {roles.map(name => (
            <MenuItem key={name} value={name}>
              <Checkbox checked={userRoles.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    );
  }

  render() {
    const header = this.createHeader()
    const { classes } = this.props;
    const numSelected = this.state.selected.length;
    const table = this.makeMuiTable(this.props.users, classes)
    const showDeleteSnackbar = this.renderSnackbar()
    if (!this.props.isUserLoggedIn) {
      return <Redirect to="/" />;
    }
    const alertWindow = this.renderAlertWindow();
    return (
      <div className={classes.mainDiv}>
             {alertWindow}
             {showDeleteSnackbar}
        <Paper className={classes.paper}>
          <EnhancedTableToolbar
            numSelected={numSelected}
            classes={classes}
            onDelete={this.openAlertWindow}
          />
          <Table className={classes.table}>
            {header}
            {table}
          </Table>
        </Paper>

      </div>
    );
  }
}

AdminPage.propTypes = {
  isUserLoggedIn: PropTypes.bool.isRequired,
  adminActions: PropTypes.object,
  users: PropTypes.array,
  classes: PropTypes.object
};


function mapStateToProps(state) {
  return {
    isUserLoggedIn: state.isUserLoggedIn,
    users: state.users
  };
}

function mapDispatchToProps(dispatch) {
  return {
    adminActions: bindActionCreators(adminActions, dispatch)
  };
}

export default withStyles(styles)(connect(
  mapStateToProps,
  mapDispatchToProps
)(AdminPage));

