import React from 'react'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import PropTypes from "prop-types";
import { Redirect } from 'react-router-dom';
import '../../styles/login-page.css';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { resolveToken } from '../../http-requests/checkToken';
import { parse } from 'query-string';
import FormControl from '@material-ui/core/FormControl';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import * as actions from '../../actions/userLoginActions';
import Snackbar from '@material-ui/core/Snackbar';



const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  }
});

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

class LoginPage extends React.Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      username: '',
      signupUsername: '',
      signupPassword: '',
      signupFullname: '',
      signupSuccessSnackbar: false,
      password: '',
      value: 0,
      vertical: 'top',
      horizontal: 'center'
    };
    this.handleChange = this.handleChange.bind(this);
    var token = window.location.search.replace("?token=", "");
    if (props.location && props.location.search) {
      const query = parse(this.props.location.search);
      if (query && query.token) {
        token = query.token;
      }
    }
    token = resolveToken(token);
    props.actions.checkToken(token);
    this.onLoginClick = this.onLoginClick.bind(this);
    this.onSignupClick = this.onSignupClick.bind(this);
    this.onFacebookLoginClick = this.onFacebookLoginClick.bind(this);
    this.handleTabChange = this.handleTabChange.bind(this);
    this.handleSnackbarClose = this.handleSnackbarClose.bind(this);
    this.activateSnackbarSignup = this.activateSnackbarSignup.bind(this);
  }

  activateSnackbarSignup() {
    this.setState({ signupSuccessSnackbar: true });
  }

  handleSnackbarClose() {
    this.setState({ signupSuccessSnackbar: false });
  }


  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  onLoginClick() {
    this.props.actions.login(this.state.username, this.state.password).then(token => {
      this.props.actions.checkToken(token);
    }
    );
  }

  onSignupClick() {
    const {signupFullname, signupUsername, signupPassword} = this.state;
    this.props.actions.signUp(signupFullname, signupUsername, signupPassword).then(response => {
      if (response === 200){
        this.setState({signupSuccessSnackbar:true, value: 0});

      }
    }
    );
  }

  onFacebookLoginClick() {
    window.location = 'http://localhost:8080/facebook/redirect';
  }

  handleTabChange(event, value) {
    this.setState({value});

  }

  render() {
    const value = this.state.value; 
    const { vertical, horizontal, signupSuccessSnackbar } = this.state;
    if (this.props.isUserLoggedIn) {
      return <Redirect to="/landing-page" />;
    }
    return (
      <div className='wrapper'>
      <div className={this.props.classes.root}>
       <AppBar position="static">
          <Tabs value={value} onChange={this.handleTabChange}>
            <Tab label="Login" />
            <Tab label="Sign-up"/>
          </Tabs>
        </AppBar>
        {value === 0 && <TabContainer>
        <div className='loginForm'>
        <h1>Login to the Great App!</h1>
          <form>
            <FormControl fullWidth>
              <TextField
                id="username"
                label="Username"
                name="username"
                value={this.state.username}
                onChange={this.handleChange}
                margin="normal"
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                id="password"
                label="Password"
                name="password"
                value={this.state.password}
                onChange={this.handleChange}
                margin="normal"
                type="password"
              />
            </FormControl>
            <FormControl>
              <Button variant="contained" color="secondary" onClick={this.onLoginClick}>
                Login
          </Button>
            </FormControl>
            <Button
              variant="contained"
              color="primary"
              onClick={this.onFacebookLoginClick}
              className={this.props.classes.button}>
              Login with facebook
          </Button>
          </form>
        </div>
        </TabContainer>}
        {value === 1 && <TabContainer>
        <div className='loginForm'>
        <h1>Sign up for an account</h1>
          <form>
            <FormControl fullWidth>
              <TextField
                id="username"
                label="Username"
                name="signupUsername"
                value={this.state.signupUsername}
                onChange={this.handleChange}
                margin="normal"
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                id="fullname"
                label="Full Name"
                name="signupFullname"
                value={this.state.signupFullname}
                onChange={this.handleChange}
                margin="normal"
              />
            </FormControl>
            <FormControl fullWidth>
              <TextField
                id="password"
                label="Password"
                name="signupPassword"
                value={this.state.signupPassword}
                onChange={this.handleChange}
                margin="normal"
                type="password"
              />
            </FormControl>
            <FormControl>
              <Button variant="contained" color="secondary" onClick={this.onSignupClick}>
                Sign up
          </Button>
            </FormControl>
          </form>
          <Snackbar
          anchorOrigin={{ vertical, horizontal }}
          open={signupSuccessSnackbar}
          onClose={this.handleSnackbarClose}
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          message={<span id="message-id">You signed up successfully!</span>}
        />
        </div>
        </TabContainer> }
      </div>
      </div>
    );
  }
}

LoginPage.propTypes = {
  history: PropTypes.object,
  isUserLoggedIn: PropTypes.bool.isRequired,
  actions: PropTypes.object.isRequired,
  location: PropTypes.object,
  classes: PropTypes.object,
};



function mapStateToProps(state) {
  return {
    isUserLoggedIn: state.isUserLoggedIn
  };
}
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default withStyles(styles)(connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginPage));

