/* eslint-disable import/no-named-as-default */
import { Route, Switch } from "react-router-dom";
import AboutPage from "./AboutPage";
import AdminPage from "./AdminPage/AdminPage";
import LoginPage from "./LoginPage/LoginPage";
import LandingPage from "./LandingPage/LandingPage";
import NotFoundPage from "./NotFoundPage";
import NavBar from './NavBar';
import PropTypes from "prop-types";
import React from "react";
import CssBaseline from '@material-ui/core/CssBaseline';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.redirectToLogin = this.redirectToLogin.bind(this);
  }


  redirectToLogin() {
    window.location = window.location.origin;
  }


  render() {
    return (
      <div className={'appclass'}>
        <NavBar />
        <Switch>
          <Route exact path="/" component={LoginPage} />
          <Route path="/landing-page" component={LandingPage} isUserLoggedIn={this.props.isUserLoggedIn}
            redirectToLogin={this.redirectToLogin} />
          <Route path="/admin" component={AdminPage} redirectToLogin={this.redirectToLogin} />
          <Route path="/about" component={AboutPage} isUserLoggedIn={this.props.isUserLoggedIn}
            redirectToLogin={this.redirectToLogin} />
          <Route component={NotFoundPage} />
          <Route path='/facebook-login' component={() => window.location = 'http://localhost:8080/facebook/redirect'} />

        </Switch>
        <CssBaseline />
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.element,
  actions: PropTypes.object,
  location: PropTypes.object,
  isUserLoggedIn: PropTypes.bool
};


export default App;
